@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

body {
  box-sizing: border-box;
  --font-color: #3d4e61;
  font-family: Avenir, sans-serif;
}

.search_bar {
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  align-items: center;
  margin-top: 20px;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search_bar--big {
  height: 70px;
  padding-left: 26px;
}

.search_bar--small {
  height: 42px;
  padding-left: 13px;
}

.search_bar__icon {
  background-image: url("Search.ccd3c07b.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
}

.search_bar__icon--small {
  width: 12px;
  height: 12px;
}

.search_bar__icon--big {
  width: 25px;
  height: 25px;
}

.search_bar__input {
  all: unset;
  color: var(--font-color);
  width: 100%;
}

.search_bar__input::placeholder {
  color: var(--font-color);
}

.search_bar__input--big {
  padding-left: 17px;
  font-size: 16px;
}

.search_bar__input--small {
  padding-left: 9px;
  font-size: 14px;
}

.search_bar:focus {
  text-shadow: 0 4px 4px #00000040;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  font-weight: 900;
  box-shadow: 0 3px 8px #3d4e6133;
}

.search_bar:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}
